@mixin button($bg:$wvu-blue, $color:$white, $hoverBg: $white, $hoverColor: $wvu-blue) {
  background-color: $bg;
  border: 1px solid $bg;
  color: $color;
  cursor: pointer;
  line-height: 1.4;
  padding: 0.5em 1em;
  display: inline-block;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  border-radius: 5px;

  &:visited {
    color: $color;
  }

  &:hover {
    background-color: $hoverBg;
    color: $hoverColor;
    border-color: $hoverColor;
  }
}

@mixin button-alt {
  @include button($wvu-gold, $wvu-blue, $wvu-blue, $wvu-gold);

  //font-weight: 600;
  //font-size: 0.7rem;
  //padding: 0.4em 0.6em 0.3em;
  border: none;
}

@mixin button-alt-2 {
  @include button($gray-lightest, $black, $gray-lighter);

  font-size: 0.8rem;
  font-weight: 600;
  text-transform: none;
}

.button {
  @include button;

  &--alt {
    @include button;
    @include button-alt;
  }

  &--alt-2 {
    @include button;
    @include button-alt-2;
  }
}
